<template>
  <div>
    <el-card class="lfont">
      <el-row>
        <el-col :offset="2">
          <h2>学生信息</h2>
        </el-col>
      </el-row>
      <el-row type="flex">

        <el-col :offset="2">

          <h1>姓名</h1>
          {{name       }}
        </el-col>
        <el-col :offset="2">
          <h1>性别</h1>
          {{sex        }}
        </el-col>
        <el-col :offset="2">
          <h1>出生日期</h1>
          {{birthday        }}
        </el-col>
        <el-col :offset="2">
          <h1>文化程度</h1>
          {{degree     }}
        </el-col>
      </el-row>
      <el-divider></el-divider>
      <el-row>
        <el-col :offset="2">
          <h2>身份信息</h2>
        </el-col>
      </el-row>
      <el-row type="flex">

        <el-col :offset="2">
          <h1>学号</h1>
          {{stuNum       }}
        </el-col>

        <el-col :offset="2">
          <h1>身份证号</h1>
          {{idcard        }}
        </el-col>

        <el-col :offset="2">
          <h1>联系方式</h1>
          {{mobile       }}
        </el-col>
        <el-col :offset="2">
          <h1>联系地址</h1>
          {{addr       }}
        </el-col>

      </el-row>
      <el-row type="flex">

        <el-col :offset="2">
          <h1>照片路径</h1>
          {{idcardPath }}
        </el-col>

        <el-col :offset="2">

          <h1>邮箱</h1>
          {{email       }}
        </el-col>
        <el-col :offset="2">
          <h1>QQ</h1>
          {{qq      }}
        </el-col>
      </el-row>
      <el-divider></el-divider>
      <el-row>
        <el-col :offset="2">
          <h2>教育信息</h2>
        </el-col>
      </el-row>
      <el-row type="flex">

        <el-col :offset="2">
          <h1>年级</h1>
          {{step }}
        </el-col>

        <el-col :offset="2">
          <h1>专业</h1>
          {{pro    }}
        </el-col>

        <el-col :offset="2">
          <h1>班级</h1>
          {{cless    }}
        </el-col>

        <el-col :offset="2">
          <h1>入学起点</h1>
          {{preDegree    }}
        </el-col>
        <el-col :offset="2">
          <h1>毕业学校</h1>
          {{school   }}
        </el-col>

      </el-row>
      <el-divider></el-divider>
      <el-row>
        <el-col :offset="2">
          <h2>状态信息</h2>
        </el-col>
      </el-row>
      <el-row type="flex">
        <el-col :offset="2">
          <h1>学生状态</h1>
          {{stuStatus  }}
        </el-col>

        <el-col :offset="2">
          <h1>教学点</h1>
          {{unit  }}
        </el-col>
        <el-col :offset="2">
          <h1>从事职业</h1>
          {{duty  }}
        </el-col>
        <el-col :offset="2">
          <h1>所在单位</h1>
          {{company  }}
        </el-col>
      </el-row>
      <el-divider></el-divider>

      <!-- {{email      }} -->
      <!-- {{cless      }} -->
      <!-- {{birthyear  }} -->
      <!-- {{qq         }} -->
      <!-- {{school     }} -->
      <!-- {{company    }}
      {{duty       }} -->
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'perInfo',
  data () {
    return {
      name: '',
      idcard: '',
      sex: '',
      email: '',
      mobile: '',
      stuNum: '',
      term: '',
      cless: '',
      pro: '',
      step: '',
      preDegree: '',
      stuStatus: '',
      unit: '',
      birthday: '',
      idcardPath: '',
      birthyear: '',
      qq: '',
      school: '',
      degree: '',
      addr: '',
      company: '',
      duty: ''
    }
  },
  mounted () {
    var id = window.sessionStorage.getItem('storeData')
    this.getPerInfo(id)
  },
  methods: {
    getPerInfo (id) {
      this.$ajax({
        url: 'http://119.29.243.108:8080/tbaps/stu/perInfo',
        method: 'get',
        params: {
          stuId: id,
        }
      }).then(res => {
        console.log(res);
        this.name = res.data.data.name
        this.idcard = res.data.data.idcard
        this.sex = res.data.data.sex
        this.email = res.data.data.email
        this.mobile = res.data.data.mobile
        this.stuNum = res.data.data.stuNum
        this.term = res.data.data.term
        this.cless = res.data.data.cless
        this.pro = res.data.data.pro
        this.step = res.data.data.step
        this.preDegree = res.data.data.preDegree
        this.stuStatus = res.data.data.stuStatus
        this.unit = res.data.data.unit
        this.birthday = res.data.data.birthday
        this.idcardPath = res.data.data.idcardPath
        this.birthyear = res.data.data.birthyear
        this.qq = res.data.data.qq
        this.school = res.data.data.school
        this.degree = res.data.data.degree
        this.addr = res.data.data.addr
        this.company = res.data.data.company
        this.duty = res.data.data.duty
        if (!this.cless) {
          this.cless = "暂无"
        }
        if (!this.school) {
          this.school = "暂无"
        }
        if (!this.company) {
          this.company = "暂无"
        }
        if (!this.duty) {
          this.duty = "暂无"
        }
        if (!this.qq) {
          this.qq = "暂无"
        }
        if (!this.idcardPath) {
          this.idcardPath = "暂无"
        }
        if (!this.addr) {
          this.addr = "暂无"
        }
        if (!this.email) {
          this.email = "暂无"
        }
      }).catch(error => {
        this.$message.error('错误')
        console.log(error);
      })

    }
  },
  components: {

  }
}
</script>

